
<template>
  <div>
    <div>
        <base-header type="gradient-default" class="pb-6 pb-8 pt-5 pt-md-6">
        
            <div class="col-lg-5 col-md-6">
              <h1 class="text-white">Smart Food Waste Bin</h1>
              
            </div>
        </base-header>
    </div>
    
    
    <div class="pb-5 pb-1 pt-5 pt-md-10">
        <div class="header-body mb-10">
            
               
        <div class="col-md-7">
              <h2 class="text-default">Amount(ml)</h2>
                <h4>
                             .
                </h4>
              <hr>
        </div>

        <div class="col-md-7">
              <h2 class="text-default">Temperature(C)</h2>
              <h4>
                              .
                </h4>
              <hr>
        </div>

        <div class="col-md-7">
              <h2 class="text-default">Term()</h2>
              <h4>
                      .
                </h4>
              <hr>
        </div>
          
        </div>
    </div>

    

    <div class="row">
        <div class="col-xl-3"></div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Input Amount" v-model="model.Amount"></base-input>
        </div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Current Amount" disabled></base-input>
        </div>
    </div>

    <div class="row">
        <div class="col-xl-3"></div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Input Temperature" v-model="model.Temperature"></base-input>
        </div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Current Temperature" disabled></base-input>
        </div>
    </div>

    <div class="row">
        <div class="col-xl-3"></div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Input Term" v-model="model.Term"></base-input>
        </div>
        <div class="col-md-3">
            <base-input formClasses="input-group-alternative" placeholder="Current Term" disabled></base-input>
        </div>
    </div>
    
    
    <div class="text-center my-3">
        <base-button type="primary" @click="modals.modal1 = true">
        Change
      </base-button>
    </div>
  
    <modal v-model:show="modals.modal1">
          <template v-slot:header>
            <h5 class="modal-title" id="modal-title-default">Settings</h5>
          </template>

          <p>       .</p>
          <p>   ?</p>

          <template v-slot:footer>
              <base-button type="primary" @click="getData">Save changes</base-button>
              <base-button type="secondary" class="ml-auto" @click="modals.modal1 = false">Close
              </base-button>
          </template>
      </modal>
    
  </div>
</template>




<script>
import axios from 'axios';

export default {
    name: "Settings",
      data() {
        return {
          model: {
            Amount: "",
            Temperature: "",
            Term: "",
          },

          modals:{
            modal1: false
          },

          
    
        };
        
      
      },
    methods: {
					getData: function() {
						axios.post('http://127.0.0.1:3000/save',{
							temp: this.model.Temperature,
							weight: this.model.Amount,
							time: this.model.Term
						})
							.then(function(response) {
								console.log(response);
							});
					}
				}

}

</script>

